.header-outer{
    min-height: 75px;
    max-height: 20vh;
    line-height: 15vh;
    width: 100vw;
    text-align: center;
    background-color: lightgrey;
    font-family: Snell Roundhand, cursive;
    font-size: 30px;
    /* font-size: 20vh; */
    box-shadow: 0px 5px 5px grey;
    color: silver;
    -webkit-text-stroke-width: 0.5px;
    -webkit-text-stroke-color: grey;
    text-shadow: 4px 5px 2px white;
}

@media (max-width:1000px){
    .header-outer{
        height: 12vh;
        line-height: 10vh;
        padding: 1vh;
    /* -webkit-text-stroke-color: grey; */
    font-size: 20px;

    }
}